<script>
import { ethers } from "ethers";
// async function fastFetch(link, data) {
//   if (data !== undefined) data = "?" + new URLSearchParams(data).toString();
//   else data = "";
//   return await fetch(address + link + data).then((response) => response.json());
// }
async function fetchAbi(address) {
  let result = await fetch(
    `https://api.bscscan.com/api?module=contract&action=getabi&address=${address}&apikey=93FBVVMWVW7M2AJ2PVVF4YRQCWZZNUN72C`
  ).then((response) => response.json());
  if (result.message == "OK") return JSON.parse(result.result);
  else return await fetchAbi(address);
}

let BNB, USDT, BUSD;
async function getBalance(fun, address, type = "bnb") {
  try {
    switch (type) {
      case "bnb":
        return await fun.getBalance(address);
      case "erc20":
        return await fun.balanceOf(address);
    }
  } catch (error) {
    return await getBalance(fun, address, type);
  }
}
export default {
  name: "App",
  components: {},
  data() {
    return {
      showSetting: true,
      address: "",
      currentAddress: [],
      usdtAddr: "0x55d398326f99059fF775485246999027B3197955",
      usdtAbi: [],
      busdAddr: "0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56",
      busdAbi: [],
      CD: 60,
      currentCD: 60,
      defaultCD: 60,
      intervalID: null,
      loading: 0,
    };
  },
  computed: {
    addressSplit() {
      let data = this.address.split("\n").map((v) => v.trim());
      return data
        .filter((v) => v !== "")
        .filter((v) => ethers.utils.isAddress(v));
    },
    addressError() {
      let data = this.address.split("\n").map((v) => v.trim());
      return data
        .filter((v) => v !== "")
        .filter((v) => !ethers.utils.isAddress(v)).length;
    },
    addressTable() {
      let total = {
        address: "Total",
        bnb: ethers.BigNumber.from(0),
        usdt: ethers.BigNumber.from(0),
        busd: ethers.BigNumber.from(0),
      };
      let totalAddress = [];
      for (const d of this.currentAddress) {
        let data = {
          address: d.address,
          bnb: 0,
          usdt: 0,
          busd: 0,
        };
        total.bnb = total.bnb.add(d.bnb);
        total.usdt = total.usdt.add(d.usdt);
        total.busd = total.busd.add(d.busd);
        data.bnb = ethers.utils.formatEther(d.bnb);
        data.usdt = ethers.utils.formatEther(d.usdt);
        data.busd = ethers.utils.formatEther(d.busd);
        totalAddress.push(data);
      }
      total.bnb = ethers.utils.formatEther(total.bnb);
      total.usdt = ethers.utils.formatEther(total.usdt);
      total.busd = ethers.utils.formatEther(total.busd);
      totalAddress.push(total);
      return totalAddress;
    },
  },
  methods: {
    save() {
      localStorage.setItem("address", this.address);
      this.currentAddress = [];
      for (const addr of this.addressSplit) {
        this.currentAddress.push({
          address: addr,
          bnb: ethers.BigNumber.from(0),
          usdt: ethers.BigNumber.from(0),
          busd: ethers.BigNumber.from(0),
        });
      }
      this.getAllBalance();
    },
    setProvider() {
      BNB = new ethers.providers.JsonRpcProvider(
        "https://bsc-dataseed.binance.org/",
        { name: "binance", chainId: 56 }
      );
      USDT = new ethers.Contract(this.usdtAddr, this.usdtAbi, BNB);
      BUSD = new ethers.Contract(this.busdAddr, this.busdAbi, BNB);
    },
    getAllBalance() {
      this.currentCD = this.CD;
      this.loading = this.currentAddress.length * 3;
      for (const key in this.currentAddress) {
        let addr = this.currentAddress[key].address;
        getBalance(BNB, addr).then(
          (bn) => ((this.currentAddress[key].bnb = bn), this.loading--)
        );
        getBalance(USDT, addr, "erc20").then(
          (bn) => ((this.currentAddress[key].usdt = bn), this.loading--)
        );
        getBalance(BUSD, addr, "erc20").then(
          (bn) => ((this.currentAddress[key].busd = bn), this.loading--)
        );
      }
    },
  },
  async mounted() {
    this.address = localStorage.getItem("address") || "";
    if (this.address !== "") this.showSetting = false;
    this.usdtAbi = await fetchAbi(this.usdtAddr);
    this.busdAbi = await fetchAbi(this.busdAddr);
    this.setProvider();
    if (this.address !== "") this.save();

    // this.intervalID = setInterval(() => {
    //   this.currentCD--;
    //   if (this.currentCD <= 0) {
    //     this.getAllBalance();
    //   }
    // }, 1000);
  },
};
</script>

